import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { redirectFromLogin } from '../redux/actions/authActions';
import FullPageSpinner from '../components/common/FullPageSpinner/FullPageSpinner';
import path from '../helpers/path';

function withAuthentication(WrappedComponent) {
  const ModifiedComponent = props => {
    const { isAuthenticated, location, pendingAuth, redirectFromLogin } = props;
    if (pendingAuth) {
      return <FullPageSpinner />;
    }
    if (!isAuthenticated) {
      redirectFromLogin(location);
      return <Redirect to={path('/login')} />;
    }

    return <WrappedComponent {...props} />;
  };

  const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    pendingAuth: state.auth.pending
  });

  const mapDispatchToProps = dispatch => ({
    redirectFromLogin: location => dispatch(redirectFromLogin(location))
  });

  return connect(
    mapStateToProps,
    mapDispatchToProps
  )(ModifiedComponent);
}

export default withAuthentication;



